.modal{
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    width: 500px;
    height: 500px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    font-weight: bold;
    color: rgb(94, 94, 94);
    -webkit-box-shadow: 3px 2px 17px -2px rgba(87,87,87,1);
    -moz-box-shadow: 3px 2px 17px -2px rgba(87,87,87,1);
    box-shadow: 3px 2px 17px -2px rgba(87,87,87,1);
}

h1{
    text-align: center;
}

img{
    width: 400px;
    height: 400px;
    border-radius: 10px;
    cursor: pointer;
}

.control{
    margin: 2px;
    padding: 10px;
    color: rgb(94, 94, 94);
    font-weight: 600;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
    border: 2px solid rgb(170, 169, 169);
}

.control:hover{
    background-color: rgb(158, 255, 190);
  }